@charset "UTF-8";
@font-face {
  font-family: "PT-Sans";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT-Sans";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
:root {
  --main-font: "PT-Sans", "Arial", sans-serif;
}

:root {
  --brand-color: #81b3d2;
  --brand-color-hover: #669ec0;
  --brand-color-active: #5496bd;
  --text-main: #000000;
  --text-secondary: #333333;
  --text-accent: #ffffff;
  --text-active: #766357;
  --bg-color: #ffffff;
  --bg-color-secondary: #eeeeee;
  --button-disabled: #e5e5e5;
  --input: #f2f2f2;
  --input-main: #e5e5e5;
  --check-color: #d5d5d5;
  --input-icon-bg: #e6e6e6;
  --form-deco: #d8d8d8;
  --copyright-active: #b3b3b3;
  --modal-bg: #f2f2f2;
  --photo-bg: #f2f2f2;
  --photo-bg-dark: #eaeaea;
  --photo-deco: #e5e5e5;
}

body {
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: var(--text-main);
  background-color: var(--bg-color);
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: contain;
  object-position: center;
}

* {
  outline: 1px solid #000000;
}

.title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}

.advantages-title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
}

.button {
  font-family: inherit;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  border: none;
  text-transform: uppercase;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button--main {
  color: var(--text-accent);
  background-color: var(--brand-color);
}
.button--main:hover {
  background-color: var(--brand-color-hover);
}
.button--main:active {
  background-color: var(--brand-color-active);
  color: rgba(255, 255, 255, 0.3);
}
.button--main:disabled {
  background-color: var(--button-disabled);
}

.label {
  color: var(--text-secondary);
  text-transform: uppercase;
}

.input {
  border: none;
  background-color: var(--input);
  font-family: inherit;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: var(--text-main);
}

.label-control {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}

.textarea {
  font-family: inherit;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: var(--main-text);
  border: none;
  resize: none;
}

.header {
  position: relative;
  min-width: 320px;
  min-height: 56px;
}

.logo {
  position: absolute;
  display: block;
  top: 0;
  left: calc(50% - 92.5px);
  max-height: 96px;
  order: 2;
  z-index: 2;
}
@media (min-width: 768px) {
  .logo {
    z-index: 10;
  }
}

.navigation {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: flex-end;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: var(--text-accent);
  text-align: center;
  text-transform: uppercase;
}
.navigation--closed .navigation__toggle::before {
  position: absolute;
  content: "";
  width: 23px;
  height: 4px;
  top: 9px;
  left: 9px;
  background-color: var(--brand-color);
  box-shadow: 0 9px 0 0 var(--brand-color), 0 18px 0 0 var(--brand-color);
}
.navigation--closed .navigation__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
}
.navigation--closed .navigation__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3), 0 18px 0 0 rgba(129, 179, 210, 0.3);
}
.navigation--closed .navigation__list {
  display: none;
}
.navigation--opened .navigation__toggle::before, .navigation--opened .navigation__toggle::after {
  position: absolute;
  content: "";
  width: 27px;
  height: 4px;
  top: 17px;
  left: 7px;
  background-color: var(--bg-color);
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}
.navigation--opened .navigation__toggle:hover::before, .navigation--opened .navigation__toggle:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.navigation--opened .navigation__toggle:active::before, .navigation--opened .navigation__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.navigation--opened .navigation__list {
  display: block;
}
.navigation--nojs .navigation__toggle {
  display: none;
}
.navigation--nojs .navigation__list {
  display: block;
  position: absolute;
  top: 0;
}
@media (min-width: 768px) {
  .navigation {
    font-size: 14px;
    line-height: 21px;
    color: var(--text-main);
  }
  .navigation--closed .navigation__list, .navigation--opened .navigation__list, .navigation--nojs .navigation__list {
    display: flex;
  }
}

.navigation__toggle {
  display: block;
  position: relative;
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  margin: 8px 13px 8px auto;
  z-index: 4;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

.navigation__wrapper {
  min-width: 100%;
}
@media (min-width: 768px) {
  .navigation__wrapper {
    min-width: 90%;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .navigation__wrapper {
    min-width: 75%;
    margin: 0 auto;
  }
}

.navigation__list {
  position: absolute;
  list-style-type: none;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
}
@media (min-width: 768px) {
  .navigation__list {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}

.navigation__item {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 55px;
  background-color: var(--brand-color);
}
.navigation__item:hover {
  background-color: var(--brand-color-hover);
}
.navigation__item:active {
  background-color: var(--brand-color-active);
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .navigation__item {
    flex-basis: 25%;
    min-height: 56px;
    margin-bottom: 0;
    background-color: var(--bg-color);
    box-shadow: none;
  }
  .navigation__item--catalog {
    margin-right: 156px;
  }
  .navigation__item:not(:last-of-type) {
    box-shadow: none;
  }
  .navigation__item:not(:first-of-type) {
    box-shadow: none;
  }
  .navigation__item:hover {
    background-color: transparent;
    color: var(--brand-color);
  }
  .navigation__item:active {
    background-color: transparent;
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 1200px) {
  .navigation__item {
    flex-basis: 25%;
  }
  .navigation__item--catalog {
    margin-right: 240px;
  }
}

.navigation__link {
  width: 100%;
  font-family: inherit;
  text-decoration: none;
  color: inherit;
  padding: 17px 0 12px;
}
@media (min-width: 768px) {
  .navigation__link {
    min-width: 153px;
    padding: 18px 0 17px;
  }
  .navigation__link--active {
    color: var(--text-active);
  }
}
@media (min-width: 1200px) {
  .navigation__link {
    min-width: 240px;
  }
}

