@media (min-width: 768px) {
  .form__container {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }
  .form__container:nth-child(n+3) {
    grid-column: 1/-1;
  }
  .form__container--impressions {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    padding: 0 36px;
  }
  .form__container--attraction {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px 32px;
    padding: 0 84px;
  }
  .form__container:first-of-type::before {
    display: block;
    position: absolute;
    content: "";
    width: 1px;
    height: 100%;
    background-color: var(--form-deco);
    top: 0;
    bottom: 0;
    right: -36px;
  }
  .form__container:nth-child(1) > .form__title::after, .form__container:nth-child(1) > .form__title::before, .form__container:nth-child(2) > .form__title::after, .form__container:nth-child(2) > .form__title::before {
    display: none;
  }
}
@media (min-width: 1200px) {
  .form__container--impressions {
    grid-template-columns: repeat(3, 1fr);
    gap: 28px;
    padding: 0 53px;
